import React from 'react'
import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card"
import { Department } from '@/entities/departments/types/department.types'

type DepartmentCardProps = {
  department: Department
}

export default function DepartmentCard({ department }: DepartmentCardProps): React.JSX.Element {
  return (
    <div>
      <Card>
  <CardHeader>
    <CardTitle>{department.name}</CardTitle>
    <CardDescription>Статус: {department.isActive ? 'Активен' : 'Неактивен'}</CardDescription>
  </CardHeader>
  <CardContent>
    <p>Родитель: {department.parentId}</p>
    <p>Путь: {department.path}</p>
    <p>Количество позиций: {department.positionCount}</p>
  </CardContent>
  <CardFooter>
    <p>Обновлено {department.updatedAt.toLocaleString()}</p>
  </CardFooter>
</Card>
    </div>
  )
}
