'use client'

import { Department } from '@/entities/departments/types/department.types'
import DepartmentCard from '@/entities/departments/ui/department.card'
import { axiosInstance } from '@/shared/api/axiosInstance'
import React, { useEffect, useState } from 'react'

export default function Page(): React.JSX.Element {
    const [departments, setDepartments] = useState<Department[]>([])

    useEffect(() => {
        const fetchDepartments = async () => {
            const data = await axiosInstance.get<Department[]>('http://localhost:5129/top-positions').then(res => res.data).catch(() => [])
            setDepartments(data)
        }
        
        fetchDepartments()
    }, [])

  return (
    <div>
        <h1>Departments</h1>
        <div className='grid grid-cols-1 gap-4 md:grid-cols-2 lg:grid-cols-3'>
            {departments.map((department) => (
                <DepartmentCard department={department} key={department.id} />
            ))}
        </div>
    </div>
  )
}
